<template>
  <FormSection
    :icon="isActive ? 'mdi-lock' : 'mdi-lock-outline'"
    :is-active="isActive"
    v-on="$listeners"
  >
    <template #title>Group Details</template>
    <template #description>
      Enhancing security, access control, communication, and collaboration
      within a digital environment
    </template>

    <!-- name -->

    <ValidationProvider
      v-slot="{ errors }"
      name="name"
      :rules="{ required: true }"
    >
      <TextField
        label="name"
        is-mandatory
        :value="name"
        :error="errors[0]"
        class="q-mb-lg"
        @input="updateName"
      />
    </ValidationProvider>

    <!-- ... -->

    <!-- description -->

    <TextAreaField
      label="description"
      :value="description"
      class="q-mb-lg"
      @input="updateDescription"
    />

    <!-- ... -->

    <!-- users -->

    <ValidationProvider
      v-slot="{ errors }"
      name="users"
      :rules="{ required: true }"
    >
      <MultiSelectField
        label="users"
        is-mandatory
        :value="users"
        :error="errors[0]"
        :options="userList"
        @input="updateUsers"
      />
    </ValidationProvider>

    <!-- ... -->
  </FormSection>
</template>

<script>
import { ValidationProvider } from "vee-validate";
import FormSection from "@/components/common/item-builder/FormSection.vue";
import TextField from "@/components/common/form/text-field/TextField.vue";
import TextAreaField from "@/components/common/form/text-area-field/TextAreaField.vue";
import MultiSelectField from "@/components/common/form/select-field/MultiSelectField.vue";

import { user } from "@/api/factory.js";

export default {
  name: "GroupDetails",

  components: {
    FormSection,
    ValidationProvider,
    TextField,
    TextAreaField,
    MultiSelectField,
  },

  props: {
    name: {
      type: String,
      required: true,
    },

    description: {
      type: String,
      required: true,
    },

    users: {
      type: Array,
      required: true,
    },

    isActive: {
      type: Boolean,
      default: false,
    },

    mode: {
      type: String,
      default: "NEW",
    },
  },

  data() {
    return {
      userList: [],
    };
  },

  created() {
    this.getUsers();
  },

  methods: {
    updateName(name) {
      this.$emit("update:name", name);
    },

    updateDescription(description) {
      this.$emit("update:description", description);
    },

    updateUsers(users) {
      this.$emit("update:users", users);
    },

    /* api functions */

    async getUsers() {
      const { error, payload } = await user.getUserList();

      if (error) {
        this.$alert.error(error);
        return;
      }

      this.userList =
        payload &&
        payload.map((user) => ({
          id: this.$nano.id(),
          label: user.value,
          value: Number(user.id),
        }));
    },

    /* ... */
  },
};
</script>

<style lang="scss" scoped></style>
